<template>
  <div class="hello" ref="first">
    <canvas class="canvas" ref="canvas">
    </canvas>
    <p class="text day">{{ dayDiff }}天</p>
    <p class="text hour">{{ hours }}时<span>{{ minutes }}分</span><span>{{ seconds }}秒</span></p>
    <div class="img-wrapper">
      <img src="../assets/first/we/23.jpg" alt="">
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      dayDiff: '',
      hours: 0,
      minutes: 0,
      seconds: 0
    }
  },
  mounted () {
    let loveArr = []
    var that = this
    function Love () {
      this.opacity = 1
      loveArr.push(this)
    }
    Love.prototype.render = function () {
      let dom = document.createElement('img')
      dom.className = 'love'
      dom.src = require('../assets/love.png')
      dom.width = 20
      dom.height = 20
      dom.style.position = 'absolute'
      dom.style.opacity = this.opacity
      dom.style.top = Math.random() * (that.$refs.first.clientHeight) + 'px'
      dom.style.left = Math.random() * (that.$refs.first.clientWidth) + 'px'
      that.$refs.first.appendChild(dom)
    }
    Love.prototype.update = function () {
      this.opacity -= 0.3
      // eslint-disable-next-line no-unused-expressions
      this.opacity < 0 ? this.godie() : ''
    }
    Love.prototype.godie = function () {
      var _this = this
      loveArr.forEach(function (value, index) {
        // eslint-disable-next-line no-unused-expressions
        value === _this ? loveArr.splice(index, 1) : ''
      })
    }

    const canvas = this.$refs.canvas
    canvas.width = this.$refs.first.clientWidth
    canvas.height = this.$refs.first.clientHeight
    const ctx = canvas.getContext('2d')
    let ballsArr = []
    function Ball (x, y) {
      this.x = x
      this.y = y
      this.r = 40
      this.color = 'rgba(' + parseInt(Math.random() * 256) + ',' + parseInt(Math.random() * 256) + ',' + parseInt(Math.random() * 256) + ', 0.8)'
      this.dx = parseInt(Math.random() * 18) - 9
      this.dy = parseInt(Math.random() * 18) - 9
      ballsArr.push(this)
    }

    Ball.prototype.update = function () {
      this.x += this.dx
      this.y += this.dy
      this.r--
      // eslint-disable-next-line no-unused-expressions
      this.r < 0 ? this.godie() : ''
    }

    Ball.prototype.render = function () {
      ctx.beginPath()
      ctx.arc(this.x, this.y, this.r, 0, Math.PI * 2, true)
      ctx.closePath()
      ctx.fillStyle = this.color
      ctx.fill()
    }

    Ball.prototype.godie = function () {
      var _this = this
      ballsArr.forEach(function (value, index) {
        // eslint-disable-next-line no-unused-expressions
        value === _this ? ballsArr.splice(index, 1) : ''
      })
    }

    setTimeout(function () {
      canvas.ontouchmove = function (event) {
        for (let i = 0; i < 10; i++) {
          // eslint-disable-next-line no-new
          new Ball(event.touches[0].pageX, event.touches[0].pageY)
        }
      }
    })
    setInterval(function () {
      ctx.clearRect(0, 0, canvas.width, canvas.height)
      for (var i = 0; i < ballsArr.length; i++) {
        ballsArr[i].update()
        ballsArr[i] && ballsArr[i].render()
      }
    }, 20)

    var f = this.$refs.first
    setInterval(function () {
      // eslint-disable-next-line no-new,no-undef
      new Love()
      for (var x = 0; x < loveArr.length; x++) {
        loveArr[x] && loveArr[x].render()
      }
      loveArr.length = 0
      var loves = document.getElementsByClassName('love')
      for (var i = 0; i < loves.length; i++) {
        // eslint-disable-next-line no-unused-expressions
        loves[i].style.opacity < 0 ? f.removeChild(loves[i]) : ''
        loves[i].style.opacity -= 0.1
      }
    }, 500)

    var _this = this
    setInterval(function () {
      _this.timeFn('2019-10-26 18:40:00')
    }, 1000)
  },
  methods: {
    timeFn: function (d1) {
      var dateBegin = new Date(d1.replace(/-/g, '/'))
      var dateEnd = new Date()
      var dateDiff = dateEnd.getTime() - dateBegin.getTime()
      this.dayDiff = Math.floor(dateDiff / (24 * 3600 * 1000))
      var leave1 = dateDiff % (24 * 3600 * 1000)
      this.hours = this.formate(Math.floor(leave1 / (3600 * 1000)))
      // 计算相差分钟数
      var leave2 = leave1 % (3600 * 1000)
      this.minutes = this.formate(Math.floor(leave2 / (60 * 1000)))
      // 计算相差秒数
      var leave3 = leave2 % (60 * 1000)
      this.seconds = this.formate(Math.round(leave3 / 1000))
      // this.time = dayDiff + ' 天 ' + hours + '小时 ' + minutes + ' 分钟 ' + seconds + ' 秒'
      // console.log(' 相差 ' + dayDiff + '天 ' + hours + '小时 ' + minutes + ' 分钟' + seconds + ' 秒')
      // console.log(dateDiff + '时间差的毫秒数', dayDiff + '计算出相差天数', leave1 + '计算天数后剩余的毫秒数'
      //   , hours + '计算出小时数', minutes + '计算相差分钟数', seconds + '计算相差秒数')
    },
    formate: function (x) {
      if (x < 10) {
        return '0' + x
      } else {
        return x
      }
    }
  },
  directives: {
    addColor: {
      bind: function (el, binding, vnode) {
        el.className = el.className + ' add-color'
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .hello{
    position: relative;
    height: 100%;
    overflow: hidden;
    .canvas{
      position: relative;
      width: 100%;
      height: 100%;
      background-color: yellowgreen;
    }
    .text{
      position: absolute;
      width: 100%;
      text-align: center;
      background: linear-gradient(to right, red, blue);
      color: white;
      /*-webkit-background-clip: text;*/
      /*color: transparent;*/
    }
    .day{
      top: 30px;
      font-size: 40px;
      font-weight: bold;
    }
    .hour{
      top: 90px;
      font-size: 30px;
    }
    .img-wrapper{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 140px;
      height: 140px;
      background-color: white;
      border-radius: 50%;
      text-align: center;
      border: 1px solid black;
      img{
        width: 120px;
        height: 120px;
        margin-top: 10px;
        border-radius: 50%;
        z-index: 10000;
        border: 1px solid black;
      }
    }
    .love{
      position: absolute;
      width: 20px;
      height: 20px;
    }
  }
</style>



