<template>
  <div id="app" ref="app">
    <swiper :options="swiperOption" ref="mySwiper" class="wrapper">
      <!-- slides -->
      <swiper-slide class="item">
        <first></first>
      </swiper-slide>
      <swiper-slide class="item">
        <second></second>
      </swiper-slide>
      <swiper-slide>
        <third></third>
      </swiper-slide>

      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
<!--    <HelloWorld/>-->
  </div>
</template>

<script>
import First from './components/first'
import Second from './components/second'
import Third from './components/third'
import {Swiper, SwiperSlide, directive} from 'vue-awesome-swiper'

export default {
  name: 'App',
  data () {
    return {
      swiperOption: {
        // some swiper options/callbacks
        // 所有的参数同 swiper 官方 api 参数
        // ...
        direction: 'vertical'
      }
    }
  },
  mounted () {
    let height = this.$refs.app.clientHeight
    this.swiperOption = {
      direction: 'vertical',
      pagination: {
        el: '.swiper-pagination',
        clickable: true
      },
      onInit: function () {
        document.getElementsByClassName('item').style.height = height
      }
    }
  },
  components: {
    Swiper,
    SwiperSlide,
    First,
    Second,
    Third
  },
  directives: {
    swiper: directive
  }
}
</script>

<style lang="scss" scoped>
  #app{
    .wrapper{
      height: 100%;
    }
  }
</style>



