<template>
  <div class="second" ref="second">
    <div class="title">you</div>
    <div class="shadow" v-show="active > -1" @click="resetActive"></div>
    <ul class="images">
      <li v-for="index in imgNum" :key="index" ref="img" @click="setActive(index)" :class="{active: index === active}">
        <img :src="index | getImg" />
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'third',
  data () {
    return {
      imgNum: 38,
      active: -1
    }
  },
  mounted () {
    for (let i = 0; i < this.imgNum; i++) {
      let w = this.$refs.second.clientWidth
      let h = this.$refs.second.clientHeight
      this.$refs.img[i].style.top = Math.random() * (h - 200) + 'px'
      this.$refs.img[i].style.left = Math.random() * (w - 125) + 'px'
      this.$refs.img[i].style.transform = `rotate(${Math.random() * 360}deg)`
    }
  },
  methods: {
    setActive: function (index) {
      this.active = index
    },
    resetActive: function () {
      this.active = -1
    }
  },
  filters: {
    getImg: function (index) {
      return require(`../assets/second/you/${index}.jpg`)
    }
  }
}
</script>

<style lang="scss" scoped>
  .second{
    position: relative;
    width: 100%;
    height: 100%;
    background-image: url("../assets/first/bg.jpg");
    background-size: 100% 100%;
    .title{
      height: 50px;
      line-height: 50px;
      text-align: center;
      background-image: url("../assets/first/title-bg.jpg");
      background-size: 100% 100%;
      color: aqua;
      font-size: 30px;
      font-weight: bold;
    }
    .shadow{
      position: absolute;
      top: 50px;
      bottom: 0;
      width: 100%;
      background-color: black;
      opacity: .5;
      z-index: 1;
    }
    ul{
      position: relative;
      width: 100%;
      height: calc(100% - 50px);
      margin: 0;
      padding: 0;
      li{
        position: absolute;
        width: 125px;
        height: 125px;
        box-shadow: 0 0 10px #000;
        list-style: none;
        img{
          width: 125px;
          height: 125px;
        }
      }
      li.active{
        left: 50% !important;
        top: 50% !important;
        transform: translate(-50%, -50%) rotate(0deg) scale(2, 2.5) !important;
        z-index: 100;
        box-shadow: 0 5px 15px rgba(0,0,0,.5);
        border-radius: 5px;
        overflow: hidden;
      }
    }
  }
</style>



